export default {
  getCartList(state) {
    return state.cartList;
  },
  getCount(state) {
    return state.cartList.length;
  },
  isAllSelected(state) {
    let flag = true;//默认店铺上的checkbox选中
    for (let item of state.cartList) {
      // 一旦发现有没有选中的，将flag置为false
      if (!item.isChecked) {
        flag = false;
      }
    }
    return flag;
  },
  selectedCount(state) {
    let count = 0;
    for (let item of state.cartList) {
      if (item.isChecked) {
        count++;
      }
    }
    return count;
  },
  totalPrice(state) {
    return state.cartList.filter(item => {
      return item.isChecked;
    }).reduce((prevValue, item) => {
      return prevValue + item.price * item.count;
    }, 0)
  }
}
