<template>
  <div class="toast" v-show="isShowToast">
    <div class="content">{{ message }}</div>
  </div>
</template>

<script>
export default {
  name: "toast",
  /*props: {
    message: {
      type: String,
      default: '提示信息',
    },
    isShow: {
      type: Boolean,
      default: false
    }
  },*/
  data() {
    return {
      message: '',
      isShowToast: false
    }
  },
  methods: {
    show(message = '', timeout = 1500) {
      this.message = message;
      this.isShowToast = true;
      setTimeout(() => {
        this.isShowToast = false;
      }, timeout);
    }
  }
}
</script>

