// 使用前先导入包
import Vue from 'vue'
import Vuex from 'vuex'
import mutations from "@/store/mutations";
import actions from "@/store/actions";
import getters from "@/store/getters";
const state = {
  tabControlIndex: 0,
  cartList: [],//==> 应该是键值对的形式 shopId-items
};

// 在Vue中注册
Vue.use(Vuex)

// 创建store对象
const store = new Vuex.Store({
  // 数据仓库
  state,
  // 定义修改数据的方法
  // mutations中的方法，尽可能完成一件事，专注一个功能
  mutations,
  // 中间人，判断处理，调用不同的mutation
  actions,
  getters
});


export default store
