// 使用静态变量，避免填写错误
import {ADD_COUNT,ADD_TO_CART} from "@/store/mutations-types";

export default {
  [ADD_TO_CART](context, selectedItem) {
    return new Promise((resolve,reject)=>{
      let oldItem = null;
      // 从cartList中查找是否有已添加的相同商品

      for (let item of context.state.cartList) {
        // 如果店铺Id、商品Id、以及产品型号一致的话，认为是同一件商品
        if (selectedItem.shopId === item.shopId &&
          selectedItem.goodsId === item.goodsId &&
          selectedItem.color.styleId === item.color.styleId &&
          selectedItem.size.sizeId === item.size.sizeId) {
          console.log('同一件商品追加,旧的数据==>', item);
          oldItem = item;
        }
      }
      // 如果之前添加过，在此基础上累加
      if (oldItem) {
        //oldItem.count += selectedItem.count;
        context.commit(ADD_COUNT,{
          oldItem:oldItem,
          count:selectedItem.count
        });
        resolve('当前商品数量+1');
      } else {
        //state.cartList.push(selectedItem);
        context.commit(ADD_TO_CART,selectedItem);
        resolve('添加了新的商品');
      }
      //console.log('VueX中的cartList==>', context.state.cartList)
    })

  },
}
