<template>
  <div class="tabbar-item" v-on:click="itemClick">
    <div v-if="isActive">
      <slot name="item-icon-active"/>
    </div>

    <div v-else>
      <slot name="item-icon"/>
    </div>

    <div v-bind:style="textStyle">
      <slot name="item-text"/>
    </div>

  </div>
</template>

<script>
export default {
  name: "TabbarItem",
  props: {
    path: {
      type: String,
      default: '/home'
    },
    textColor: {
      type: String,
      default: '#ff5777'
    }
  },
  computed: {
    isActive() {
      return this.path.indexOf(this.$route.path) !== -1;
    },
    textStyle() {
      return this.isActive ? {color: this.textColor} : {}
    }
  }
  ,
  methods: {
    itemClick() {
      console.log('itemClick--', this.path)
      this.$router.replace(this.path)
    }
  }
}
</script>

<style scoped>

.tabbar-item {
  flex: 1;
  text-align: center;
}

.tabbar-item img {
  width: 24px;
  height: 24px;
  vertical-align: middle;
  margin-top: 3px;
  margin-bottom: 2px;
}

.tabbar-item div {
  font-size: 14px;
}

.text-active {
  color: #ff5777;
}
</style>
